//Hasta Luego Mixins
@mixin goingLarge($width) {
  @media (min-width: #{$width / 16}em) { @content; }

}

//Remove outline from button and link when in focus or active
button:active, button:focus, a:active, a:focus {
  outline: none;
}

//Heading Typography
@mixin heading-style {
  font-size: 2rem;
  @include goingLarge(600) {
    font-size: 2.8rem;
    line-height: 40px;
  }
}

//Link effect
@mixin link-style {
  display: inline-block;
  color: #000;
  text-decoration: none;
  font-weight: 500;
  padding: 5px 10px;  
  border-radius: 5px;
  position: relative;
  transition: all .3s;
  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    bottom: 0px;
    left: 0;
    z-index: -1;
    background-color: rgba(12, 0, 177, 0.5);
    border-radius: 3px;
    transition: all 0.3s;
    @include goingLarge(1000) {
      height: 5px;
      transform: scaleX(.9);
    }
  }
  &::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: #000;
    border-radius: 3px;
    transform: scaleX(0);
    visibility: hidden;
    transition: all 0.3s ease-in-out .0s;
  }
  &:hover {
  &::before {
    background-color: rgba(12, 0, 177, 0.7);    
    transform: scaleX(1);
    height: 100%;
  }
  &::after {
    visibility: visible;
    transform: scaleX(1);
  }
  }
}

//Page side-title
@mixin page-tag {
  display: none;
  text-shadow: 2px 6px 10px #bd26e2;
  @include goingLarge(1000) {
    display: block;
    font-size: 5rem;
    transform: rotate(-90deg);
    position: fixed;
    top: 40%;
    font-weight: 700;
    opacity: 0.1;
    left: -130px;  
  }
  @include goingLarge(1400) {
    font-size: 6rem;
  }
}

.projects-page {
  height: 100vh;
  position: relative;
  color: #000;
  padding: 30px 0;
  @include goingLarge(1000) {
    padding: 40px 100px 30px;
  }
  @include goingLarge(1400) {
    padding: 100px 200px 60px;
  }
  p.page-tag {
    @include page-tag;
  }
  .page-content {
    position: relative;
    height: 100%;
    padding-left: 20px;
    @include goingLarge(1100) {
      padding-left: 40px;
    }
    h1 {
      @include heading-style;
      .glass-svg {
        width: 30px;
        height: 30px;
        vertical-align: top;
        position: relative;
        @include goingLarge(600) {
          width: 40px;
          height: 40px;
        }
        .emoji-smily {
          animation: muhaha 6s infinite;
        }
      }
    }
    p {
      font-size: 1.2rem;
      &.page-intro {
        padding-right: 20px;
      }
      @include goingLarge(800) {
        font-weight: 500;
        font-size: 1.5rem;
      }
    }
    .projects-container {
      padding-top: 50px;
      .project-item {
        padding-right: 20px;
        margin-bottom: 80px;
        &::after {
          content: "";
          display: block;
          height: 3px;
          width: 250px;
          margin: 0 auto;
          background-color: #000;
          border-radius: 5px;
        }
        .project-heading {
          h2 {
            font-size: 1.8rem;
            &::before{
              content: "";
              display: inline-block;
              vertical-align: top;
              margin-right: 10px;
              width: 5px;
              height: 30px;
              z-index: 1;
              background-color: #1ecbff;
              box-shadow: -2px 3px 15px 3px rgba(174, 236, 255, 0.612);      
            }
          }
        }
        .thumb {
          display: grid;
          justify-items: center;
          margin-bottom: 20px;
          img {
            max-width: 100%;
            height: auto;
            margin: 0 auto;
          }
        }
        .project-links {
          text-align: center;
          a {
            @include link-style;
            font-weight: 700;
            margin: 5px;
            @include goingLarge(800) {
              font-size: 1.5rem;
              font-weight: 500;
            }
          }
        }
        .project-details {
          padding-bottom: 20px;
          p {
            font-weight: 300;
          }
        }
      }
    }
  }
}

