.topnav {
  position: fixed;
  height: 4%;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  background-color: #80FFFF;
  padding-top: 30px;
  
}

 
.button:hover {
  background-color:#C5FFFF;
}

 
.button {
  flex: 1;
  display: flex;  /* Enables flexbox */
  align-items: center;  /* Centers text vertically */
  justify-content: center; /* Centers text horizontally */
  color: black;
  font-weight: 370;
  cursor: pointer;
  background-color: #80FFFF; /* Default color */
  height: 100%;
  width: 100%;
}


.button:hover {
  background-color: #C5FFFF; /* Hover color */
}

.button.active {
  background-color: #C5FFFF; /* Active color matches hover */
}

.button-container:hover .button.active {
  background-color: #80FFFF;  
}