.btn-back {
  outline: none;
  border: none; 
  background-color: #fff;
  padding: 10px;
  margin-bottom: 20px;
  border-radius: 50%;
  cursor: pointer;
  box-shadow: 1px 2px 12px 2px #e9feff96;
  svg {
    vertical-align: middle;
    z-index: 1 !important;
    position: relative !important;
    opacity: 1;
  }
}