table {
    font-family: arial, sans-serif;
    border-collapse: collapse;
    width: 100%;
    border-top: 1px solid #dddddd;
    border-bottom: 1px solid #dddddd;
    border-left: none;
    border-right: none;
}

td, th {
    border-left: none;
    border-right: none;
    border-top: 1px solid #dddddd;
    border-bottom: 1px solid #dddddd;
    text-align: left;
    padding: 8px;
}

tbody tr:hover td {
    color: #05A2A2; // Change only text color
    cursor: pointer;
}